* {
  box-sizing: border-box;
  font-weight: 300;
}

.form-field {
  position: relative;
}

.image {
    position: absolute;
    width: 19px;
    height: 19px;
    top: 25px;
    left: 26px;
    border: 1px;
  }

.image-small {
  position: absolute;
  width: 11px;
  height: 11px;
  top: 26px;
  left: 13px;
}

.input {
  font-family: "Avenir";
  font-style: normal;
  font-size: 16px;
  line-height: 22px;
  color: #3d4e61;
  margin-bottom: 20px;
  margin-top: 20px;
  background: #ffffff;
  border: 1px solid #e1e7ed;
  box-shadow: 0px 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
}

.input-small {
  font-family: "Avenir";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #3d4e61;
}

.input:hover {
  box-shadow: 0px 3px 8px rgba(61, 78, 97, 0.2);
}

.input:focus {
  background: linear-gradient(180deg, #ffffff 0%, #f6f6f7 100%);
  border-radius: 3px;
}
